import { a as client } from "../../../../../chunks/connection.js";
import { r as redirect } from "../../../../../chunks/index.js";
import { c as createAuthToken } from "../../../../../chunks/Authentication.js";
const load = ({ locals }) => {
  if (locals.user) {
    throw redirect(302, "/");
  }
  if (locals.admin) {
    throw redirect(302, "/beprofessional/admin");
  }
};
const actions = {
  default: async ({ request, cookies }) => {
    const data = await request.formData();
    const username = data.get("username");
    const password = data.get("password");
    if (!username || !password) {
      return {
        success: false,
        message: "All fields are required."
      };
    }
    const admin = await client.admins.findFirst({
      where: {
        username
      }
    });
    if (!admin) {
      return {
        success: false,
        message: "User not found"
      };
    }
    if (password !== admin.password) {
      return {
        success: false,
        message: "Invalid credentials"
      };
    }
    const token = createAuthToken(admin.admin_id, admin.username);
    const b64 = btoa(admin.username);
    cookies.set("admin", b64 + "." + token, {
      path: "/",
      httpOnly: true,
      sameSite: "strict",
      secure: false,
      //TODO : to be changed,
      maxAge: 60 * 60 * 24 * 3
    });
    throw redirect(302, "/beprofessional/admin");
  }
};
export {
  actions,
  load
};
